import React, { useState, useEffect, useRef } from "react";
import Header from "../Components/Header";
import Footer from "../Components/Footer";
import "./note.css";

export default function Todo() {
  const todoListRef = useRef(null);
  const searchFormRef = useRef(null);
  const [toDos, setToDos] = useState([]);
  const [amount, setAmount] = useState({
    input: "",
    searchKey: "",
  });
  const TODOS_KEY = "todos";

  const handleSearch = (e) => {
    e.preventDefault();
    const findKey = document.getElementById("findKey");
    let key = {
      value: "",
      id: "",
      number: "",
    };
    if (amount.searchKey) {
      let i = toDos.length;
      toDos.forEach((value) => {
        if (amount.searchKey === value.value) {
          key.value = value.value;
          key.id = value.id;
          key.number = i;
          findKey.innerText = `${key.number}. ${key.value} (ID: ${key.id})`;
        }
        i--;
      });
    }
    setAmount({ searchKey: "" });
  };

  const onChange = (e) => {
    const word = {};
    word[e.target.id] = e.target.value;
    setAmount(word);
  };

  function saveToDos(todo) {
    localStorage.setItem(TODOS_KEY, JSON.stringify([...toDos, todo]));
  }

  const deleteToDo = (e) => {
    e.preventDefault();
    const searchForm = searchFormRef.current;
    const loadToDos = localStorage.getItem(TODOS_KEY);
    const parsedToDos = JSON.parse(loadToDos);
    const li = e.target.parentElement;
    const newToDos = parsedToDos.filter((todo) => todo.id !== parseInt(li.id));

    if (newToDos.length) {
      setToDos(newToDos);
      localStorage.setItem(TODOS_KEY, JSON.stringify(newToDos));
    } else {
      searchForm.classList.add("hidden");
      localStorage.removeItem(TODOS_KEY);
    }
    li.remove();
  };

  const paintToDo = (newToDo) => {
    const searchForm = searchFormRef.current;
    const todoList = todoListRef.current;
    const li = document.createElement("li");
    li.id = newToDo.id;
    const span = document.createElement("span");
    span.innerText = newToDo.value;
    const button = document.createElement("button");
    button.innerText = "❌";
    button.addEventListener("click", deleteToDo);
    li.appendChild(span);
    li.appendChild(button);
    todoList.prepend(li);
    if (toDos.length) {
      searchForm.classList.remove("hidden");
    }
  };

  const handleToDoSubmit = (e) => {
    e.preventDefault();
    if (amount.input) {
      const newToDosObj = {
        value: amount.input,
        id: Date.now(),
      };
      setAmount({ input: "" });
      setToDos((prev) => [...prev, newToDosObj]);
      saveToDos(newToDosObj);
      paintToDo(newToDosObj);
    }
  };

  const storeToDos = localStorage.getItem(TODOS_KEY);
  const loadToDos = async () => {
    const parsedToDos = await JSON.parse(storeToDos);
    let newTodos = [];
    for (let i = 0; i < parsedToDos.length; i++) {
      newTodos.push(parsedToDos[i]);
    }
    setToDos(newTodos);
    parsedToDos.forEach(paintToDo);
  };

  useEffect(() => {
    const searchForm = searchFormRef.current;
    if (storeToDos) {
      loadToDos();
      searchForm.classList.remove("hidden");
    }
  }, []);
  return (
    <>
      <Header />
      <main className="noteMain">
        <section className="noteSection">
          <h1 className="noteTitle animation_shadowEffect">
            Note
            <span className="animation_lightEffect"></span>
          </h1>
          <div className="formBox">
            <article className="inputBox">
              <form className="inputForm">
                <label>
                  <h1>Text</h1>
                  <input
                    id="input"
                    className="input"
                    type="text"
                    onChange={onChange}
                    value={amount.input}
                  />
                </label>
                <input type="submit" value="" onClick={handleToDoSubmit} />
              </form>
            </article>
            <article className="searchBox">
              <form id="searchForm" ref={searchFormRef} className="hidden">
                <label>
                  <h1>Search</h1>
                  <input
                    id="searchKey"
                    className="searchKey"
                    type="text"
                    onChange={onChange}
                    value={amount.searchKey}
                  />
                </label>
                <input type="submit" value="" onClick={handleSearch} />
                <p id="findKey"></p>
              </form>
            </article>
          </div>
          <div className="listBox">
            <ol id="todo_list" ref={todoListRef}></ol>
          </div>
        </section>
      </main>
      <Footer />
    </>
  );
}
