import { BrowserRouter } from "react-router-dom";
import * as ReactDOM from "react-dom";
import * as React from "react";
import { Provider } from "react-redux";
import { createStore } from "redux";

import App from "./App";
import "./index.css";
import "./animate.css";

function reducer(state = "home", action) {
  if (action.type === "react") {
    state = "react";
    return state;
  } else if (action.type === "movie:id") {
    state = "movie:id";
    return state;
  } else if (action.type === "javascript") {
    state = "javascript";
    return state;
  } else if (action.type === "css") {
    state = "css";
    return state;
  } else if (action.type === "cssimg1") {
    state = "cssimg1";
    return state;
  } else if (action.type === "cssimg2") {
    state = "cssimg2";
    return state;
  } else if (action.type === "cssimg3") {
    state = "cssimg3";
    return state;
  } else if (action.type === "note") {
    state = "note";
    return state;
  } else if (action.type === "home") {
    state = "home";
    return state;
  } else {
    return state;
  }
}
let store = createStore(reducer);

const root = document.getElementById("root");
ReactDOM.render(
  <BrowserRouter>
    <Provider store={store}>
      <App />
    </Provider>
  </BrowserRouter>,
  root
);
