import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import React, { useState } from "react";
import "./Header.css";

export default function Header() {
  const dispatch = useDispatch();

  const [navBar, setNavBar] = useState(false);
  const viewNavBar = () => {
    const navList = document.querySelector(".listBox");
    const navTag = document.querySelectorAll(".listBox ul li a");
    const btn = document.querySelector(".listBox div");

    if (!navBar) {
      navList.classList.add("onNav");
      navTag.forEach((value) => {
        value.classList.add("onNavTag");
      });
      btn.classList.add("onBtn");
      setNavBar(true);
    } else {
      navList.classList.remove("onNav");
      navTag.forEach((value) => {
        value.classList.remove("onNavTag");
      });
      btn.classList.remove("onBtn");
      setNavBar(false);
    }
  };
  return (
    <header>
      <section className="titleBox">
        <h1>
          <Link
            to="/"
            onClick={() => {
              dispatch({ type: "home" });
            }}
          >
            Portfolio<span></span>
          </Link>
        </h1>
      </section>
      <section className="listBox">
        <ul>
          <li>
            <Link
              to="/react"
              onClick={() => {
                dispatch({ type: "react" });
              }}
            >
              React<span></span>
            </Link>
          </li>
          <li>
            <Link
              to="/javascript"
              onClick={() => {
                dispatch({ type: "javascript" });
              }}
            >
              JavaScript<span></span>
            </Link>
          </li>
          <li>
            <Link
              to="/note"
              onClick={() => {
                dispatch({ type: "note" });
              }}
              // Header="--s:2;"
            >
              Note<span></span>
            </Link>
          </li>
          <li>
            <Link
              to="/css"
              onClick={() => {
                dispatch({ type: "css" });
              }}
              // Header="--s:3;"
            >
              Css<span></span>
            </Link>
          </li>
        </ul>
        <div onClick={viewNavBar} className="navBtn">
          <span></span>
          <span></span>
        </div>
      </section>
    </header>
  );
}
