import React, { useRef, useState, useEffect } from "react";
import "./JavaScriptPage.css";
import Header from "../Components/Header";
import Footer from "../Components/Footer";

export default function JavaScriptPage() {
  const FOCUS = "onFocus";
  const BASIC_COLOR = "#2c2c2c";
  const canvasRef = useRef(null);
  const [ctx, setCtx] = useState();
  const [canvas, setCanvas] = useState();
  const [range, setRange] = useState("5");
  const [painting, setPainting] = useState(false);
  const [filling, setFilling] = useState(false);

  useEffect(() => {
    const newCanvas = canvasRef.current;
    newCanvas.width = newCanvas.clientWidth;
    newCanvas.height = newCanvas.clientHeight;
    const context = newCanvas.getContext("2d");
    context.fillStyle = "white";
    context.fillRect(0, 0, newCanvas.width, newCanvas.height);
    context.strokeStyle = BASIC_COLOR;
    context.fillStyle = BASIC_COLOR;
    context.lineWidth = range;
    setCanvas(newCanvas);
    setCtx(context);
  }, []);

  const colorClick = (e) => {
    const color = e.target.style.backgroundColor;
    const prevFocus = document.querySelector(`.${FOCUS}`);
    prevFocus.classList.remove(FOCUS);
    ctx.strokeStyle = color;
    ctx.fillStyle = color;
    e.target.classList.add(FOCUS);
  };
  const handleRange = (e) => {
    const size = e.target.value;
    ctx.lineWidth = size;
    setRange(size);
  };
  const onMouseMove = ({ nativeEvent }) => {
    const { offsetX, offsetY } = nativeEvent;
    if (!painting) {
      ctx.beginPath();
      ctx.moveTo(offsetX, offsetY);
    } else {
      ctx.lineTo(offsetX, offsetY);
      ctx.stroke();
    }
  };
  const startPainting = (e) => {
    if (e.button === 0) {
      if (!filling) {
        setPainting(true);
      } else {
        ctx.fillRect(0, 0, canvas.width, canvas.height);
      }
    }
  };
  const stopPainting = () => {
    setPainting(false);
  };
  const clearBtn = () => {
    const prevColor = ctx.fillStyle;
    ctx.fillStyle = "white";
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    ctx.fillStyle = prevColor;
  };
  const modeClick = (e) => {
    const rangeBar = document.getElementById("jsRange");
    if (filling === true) {
      setFilling(false);
      e.target.innerText = "Pen";
      rangeBar.classList.remove("hidden");
    } else {
      setFilling(true);
      e.target.innerText = "Paint";
      rangeBar.classList.add("hidden");
    }
  };

  const handleRClick = () => {
    const image = canvas.toDataURL();
    const link = document.createElement("a");
    link.href = image;
    link.download = "PaintJS[🎨]";
    link.click();
  };
  const eraserClick = () => {};
  return (
    <>
      <Header />
      <main>
        <section className="canvasSection">
          <article className="controls">
            <ul className="controls_colors" id="jsColors">
              <li
                className="controls_color jsColor onFocus"
                style={{ backgroundColor: "rgb(0, 0, 0)" }}
                onClick={colorClick}
              ></li>
              <li
                className={"controls_color jsColor"}
                style={{ backgroundColor: "rgb(255, 255, 255)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(255, 0, 0)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(255, 150, 0)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(255, 255, 0)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(0, 255, 0)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(0, 0, 255)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(20, 20, 150)" }}
                onClick={colorClick}
              ></li>
              <li
                className="controls_color jsColor"
                style={{ backgroundColor: "rgb(100, 20, 220)" }}
                onClick={colorClick}
              ></li>
            </ul>
            <div className="controls_btns">
              <input
                type="range"
                id="jsRange"
                min="1"
                max="9"
                value={range}
                step="1"
                onChange={handleRange}
              />
              <button id="jsMode" onClick={modeClick}>
                Pen
              </button>
              <button id="jsClear" onClick={clearBtn}>
                Clear
              </button>
              <button id="jsSave" onClick={handleRClick}>
                Save
              </button>
            </div>
          </article>
          <canvas
            ref={canvasRef}
            className="canvas"
            onMouseMove={onMouseMove}
            onMouseDown={startPainting}
            onMouseUp={stopPainting}
            onMouseLeave={stopPainting}
            onTouchStart={startPainting}
            onTouchMove={onMouseMove}
            onTouchEnd={stopPainting}
          ></canvas>
        </section>
      </main>
      <Footer />
    </>
  );
}
