import Header from "../Components/Header";
import Footer from "../Components/Footer";
import styles from "./CssPage.module.css";

export default function CssPage() {
  // const tag = useSelector((state) => state);
  return (
    <>
      <Header />
      <main>
        <section className={styles.loader}>
          <div className={styles.loaderBox}>
            <div className={styles.rotation1}></div>
            <div className={styles.rotation2}></div>
            <div className={styles.rotation3}></div>
            <h1 className={styles.Phrase}>C S S</h1>
          </div>
        </section>
      </main>
      <Footer />
    </>
  );
}
