import { Routes, Route } from "react-router-dom";
import Home from "./Router/Home";
import ReactPage from "./Router/ReactPage";
import JavaScriptPage from "./Router/JavaScriptPage";
import Note from "./Router/Note";
import CssPage from "./Router/CssPage";
import CssImg1 from "./Components/CssPages/CssImg1";
import CssImg2 from "./Components/CssPages/CssImg2";
import CssImg3 from "./Components/CssPages/CssImg3";

function App() {
  return (
    <Routes>
      <Route path="/" exact element={<Home />} />
      <Route path="/react" exact element={<ReactPage />} />
      <Route path="/react/:id" element={<ReactPage />} />
      <Route path="/javascript" element={<JavaScriptPage />} />
      <Route path="/note" element={<Note />} />
      <Route path="/css" element={<CssPage />} />
      <Route path="/css/cssimg1" element={<CssImg1 />} />
      <Route path="/css/cssimg2" element={<CssImg2 />} />
      <Route path="/css/cssimg3" element={<CssImg3 />} />
    </Routes>
  );
}

export default App;
