import React, { useEffect, useState, memo } from "react";
import Movie from "../Components/Movies/Movie";
import Detail from "../Components/Movies/Detail";
import styles from "./ReactPage.module.css";
import Header from "../Components/Header";
import Footer from "../Components/Footer";

function Movies() {
  const [loading, setLoading] = useState(true);
  const [rating, setRating] = useState("9");
  const [movies, setMovies] = useState([]);

  const onSelect = (e) => {
    setRating(e.target.value);
    setLoading(true);
    return rating;
  };

  const getMovies = async () => {
    const json = await (
      await fetch(
        `https://yts.mx/api/v2/list_movies.json?minimum_rating=${rating}&sort_by=year&limit=50`
      )
    ).json();
    setMovies(json.data.movies);
    setLoading(false);
  };

  useEffect(() => getMovies(), [rating]);
  return (
    <>
      <Header />
      <main className={styles.reactMain}>
        <section className={styles.container}>
          <article className={styles.navBar}>
            <div>
              <h2>영화목록</h2>
              <span>평점 </span>
              <select value={rating} onChange={onSelect}>
                <option value="9"> 9점 </option>
                <option value="8"> 8점 </option>
                <option value="7"> 7점 </option>
                <option value="6"> 6점 </option>
                <option value="5"> 5점 </option>
                <option value="4"> 4점 </option>
              </select>
              <span> 이상</span>
            </div>
          </article>
          {loading ? (
            <article className={styles.loader} style={{ height: "80vh" }}>
              <div className={styles.loaderBox}>
                <div className={styles.rotation1}></div>
                <div className={styles.rotation2}></div>
                <div className={styles.rotation3}></div>
                <h1 className={styles.Phrase}>Loading...</h1>
              </div>
            </article>
          ) : (
            <article className={styles.movies}>
              {movies.map((movie) => (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  year={movie.year}
                  coverImg={movie.medium_cover_image}
                  title={movie.title}
                  summary={movie.summary}
                  genres={movie.genres}
                />
              ))}
            </article>
          )}
        </section>
        <Detail />
      </main>
      <Footer />
    </>
  );
}

export default memo(Movies);
