import Header from "../Header";
import Footer from "../Footer";

import styles from "./CssImg1.module.css";

export default function CssPage1() {
  return (
    <>
      <Header />
      <main>
        <article className={styles.loader}>
          <div className={styles.loaderBox}>
            <div className={styles.rotation1}></div>
            <div className={styles.rotation2}></div>
            <div className={styles.rotation3}></div>
            <h1 className={styles.Phrase}>Loading...</h1>
          </div>
        </article>
      </main>
      <Footer />
    </>
  );
}
