import { useEffect, useState, memo } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import styles from "./Detail.module.css";

function Detail() {
  const dispatch = useDispatch();
  const { id } = useParams();
  const [loading, setLoading] = useState(true);
  const [movie, setMovie] = useState();

  const getMovie = async () => {
    const json = await (
      await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)
    ).json();
    setMovie(json.data.movie);
    setLoading(false);
  };

  function ClosedBtn() {
    const detail = document.getElementById("detail");
    detail.style =
      "backgroundColor:background-color: rgba(60, 60, 60, 0); overflow:hidden; right:-100%; height:0; transition:0s all";
    dispatch({ type: "react" });
    const root = document.getElementById("root");
    root.style = "overflow:hidden auto";
    setLoading(true);
  }

  useEffect(() => getMovie(), [id]);
  return (
    <section id="detail" className={styles.detail}>
      {loading ? (
        <article className={styles.loader}>
          <div className={styles.loaderBox}>
            <div className={styles.rotation1}></div>
            <div className={styles.rotation2}></div>
            <div className={styles.rotation3}></div>
            <h1 className={styles.Phrase}>Loading...</h1>
          </div>
        </article>
      ) : (
        <div>
          <article
            className={styles.detailBox}
            style={{
              background: `url(${movie.large_cover_image}) no-repeat 0 0 / 100% 100%`,
            }}
          ></article>
          <article className={styles.titleBox}>
            <h1>{movie.title}</h1>
            <div className={styles.infoBox}>
              <div>
                <span>개봉연도 : {movie.year} </span>
                <span>평 점 : {movie.rating}⭐</span>
              </div>
              <div>
                <span>장르 : </span>
                <ul>
                  {movie.genres.map((g) => (
                    <li key={g}>{g}</li>
                  ))}
                </ul>
              </div>
              <div>
                <span>링크 : </span>
                {movie.torrents[0].url ? (
                  <a href={movie.torrents[0].url} target={`_blank`}>
                    Torrent
                  </a>
                ) : null}
                {movie.yt_trailer_code ? (
                  <a
                    href={`https://youtu.be/${movie.yt_trailer_code}`}
                    target={`_blank`}
                  >
                    Youtube
                  </a>
                ) : null}
              </div>
            </div>
            <div>
              <span>영화설명 : </span>
              <p>{movie.description_full}</p>
            </div>
          </article>
          <Link to="/react" onClick={ClosedBtn}>
            ❌
          </Link>
        </div>
      )}
    </section>
  );
}
export default memo(Detail);
